td {
  display: block;
}
.board_list_div {
  /* board_list의 width + margin + border */
  width: 100%;
  min-width: 320px;
  max-width: 1280px;
  margin: auto;
  align-items: center;
  text-align: center;
}
.board_list {
  display: inline-block;
  margin: 5px;
  width: 300px;
  height: 200px;
  border: 1px solid black;
  border-radius: 10%;
}
.board_list:hover {
  background-color: #337ab7;
  cursor: pointer;
}
/* 페이징 */
.pagination {
  display: flex;
  justify-content: center;
  margin-top: 15px;
}

ul {
  list-style: none;
  padding: 0;
}

ul.pagination li {
  display: inline-block;
  width: 30px;
  height: 30px;
  border: 1px solid #e2e2e2;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
}

ul.pagination li:first-child {
  border-radius: 5px 0 0 5px;
}

ul.pagination li:last-child {
  border-radius: 0 5px 5px 0;
}

ul.pagination li a {
  text-decoration: none;
  color: #337ab7;
  font-size: 1rem;
}

ul.pagination li.active a {
  color: white;
}

ul.pagination li.active {
  background-color: #337ab7;
}

ul.pagination li a:hover,
ul.pagination li a.active {
  color: blue;
}

.page-selection {
  width: 48px;
  height: 30px;
  color: #337ab7;
}

/* 검색창 */
#main_search {
  display: block;
  max-width: 1280px;
}
