/* 기본 폼 양식 */
.default_form {
  margin: auto;
  width: 600px;
  background: grey;
  /* text-align: center; */
  font-size: 18px;
}

.default_form > form > ul {
  width: 600px;
}
.default_form > form > ul > li {
  float: left;
  list-style-type: none;
  padding: 20px;
}

.default_form > form > ul > li > input {
  margin-left: 20px;
  /* float: right; */
  height: 30px;
}

.default_form > form > ul > li > select {
  /* float: right; */
  margin-left: 20px;
  height: 30px;
  width: 177px;
}

/* content 작성칸 크기 */
.create_board {
  width: 80%;
  height: 400px;
  margin: auto;
  margin-bottom: 20px;
  resize: none;
  display: block;
}

form > .submit {
  display: block;
  font-size: 16px;
  width: 100px;
  height: 30px;
  /* margin-left: 45%; */
  align-items: center;
  align-self: center;
  align-content: center;
  /* align-tracks: center; */
  text-align: center;
  margin: auto;
}
